.pg2Container {
  /* border: solid red; */
  height: 552px;
  width: 100%;
  margin-top: 663px;
  background-color: silver;
  background: linear-gradient(to right, white, silver);
}

.headerPg2Container {
  /* border: solid; */
  height: 16vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.headerPg2Content {
  /* border: solid; */
  border-right: dotted 2px;
  height: 100%;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  background-color: #383d44;
  transition: 600ms ease-in-out;
}

.headerPg2Item {
  /* border: solid; */
  margin-left: 20px;
  color: white;
}

.headerPg2Content:hover {
  background-color: #f7894e;
  width: 27%;
}

.pg2MainContainer {
  border: solid green;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 450px;
}

.pg2MainContent {
  height: 100%;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#pg2MainContent1 {
  background-image: url("http://dotrex.co/theme-preview/eventer/img/conference-about.jpg");
  background-size: cover;
}
.pg2MainContent > img {
  width: 100%;
  height: 100%;
}

.pg2MainItem {
  /* border: solid; */
  width: 80%;
}

#pg2MainItem1 {
  height: 55%;
  position: relative;
}

.pg2MainContentDot {
  border: solid grey;
  position: absolute;
  height: 15px;
  width: 15px;
  top: 12px;
  left: 0px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pg2MainContentInnerDot {
  border: solid grey;
  border-radius: 100px;
}

.pg2MainContentLine {
  border-top: 2px solid;
  height: 20px;
}

#pg2MainItem1 > h1 {
  margin-left: 20px;
  color: #3e4651;
}

#pg2MainItem1 > span {
  color: #3e4651;
  margin-top: 10px;
  letter-spacing: 1px;
  font-size: small;
}

#pg2MainItem2 {
  height: 25%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.pg2MainBut1 {
  border: 2px solid;
  margin-left: 10px;
  margin-top: 10px;
  height: 40px;
  width: 180px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #3e4651;
  font-size: small;
  position: relative;
  overflow: hidden;
}

.pg2MainBut2 {
  border: 2px solid;
  margin-left: 10px;
  margin-top: 10px;
  height: 40px;
  width: 110px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: #3e4651;
  font-size: small;
  position: relative;
  overflow: hidden;
}

.pg2MainInnerbut {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: absolute;
  z-index: 2;
  transition: 600ms ease-in-out;
}

.pg2MainButCover {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #3e4651;
  transition: 600ms ease-in-out;
  z-index: 1;
  transform: translateX(-101%);
}

.pg2MainBut1:hover .pg2MainButCover {
  transform: translateX(0);
}

.pg2MainBut1:hover .pg2MainInnerbut {
  color: white;
}

.pg2MainBut2:hover .pg2MainButCover {
  transform: translateX(0);
}

.pg2MainBut2:hover .pg2MainInnerbut {
  color: white;
}

@media screen and (max-width: 600px) {
  .pg2Container {
    height: 580px;
    width: 700px;
    margin-top: 775px;
  }

  .headerPg2Container {
    height: 70vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
  }

  .headerPg2Content {
    box-shadow: 0px -9px 20px black;
    height: 80%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .headerPg2Content:hover {
    background-color: #383d44;
    width: 100%;
  }

  .pg2MainContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 900px;
  }

  .pg2MainContent {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  }

  .pg2MainItem {
    border: solid transparent;
    width: 690px;
    margin-top: 15px;
    margin-left: 10px;
  }

  #pg2MainItem1 {
    height: 85%;
    position: relative;
  }

  .pg2MainContentDot {
    border: 3px solid grey;
    height: 25px;
    width: 25px;
    top: 17px;
    left: 5px;
  }

  .pg2MainContentInnerDot {
    background-color: grey;
    border-radius: 100px;
    width: 8px;
    height: 8px;
  }

  #pg2MainItem1 > h1 {
    font-size: 50px;
    margin-left: 50px;
    color: #3e4651;
  }

  #pg2MainItem1 > span {
    color: #3e4651;
    margin-top: 10px;
    letter-spacing: 1px;
    font-size: x-large;
  }

  #pg2MainItem2 {
    height: 25%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -6px;
  }

  .pg2MainBut1 {
    height: 50px;
    width: 250px;
    font-size: 18px;
    font-weight: bold;
  }

  .pg2MainBut2 {
    height: 50px;
    width: 180px;
    font-size: 18px;
    font-weight: bold;
  }

  .pg2MainIcon {
    height: 20px;
  }
}

